<template>
  <div id="app">
    <div class="center">
      <div>
        <h2>Citypicker</h2>
        <img src="./assets/images/logo.png" class="logo" alt="">
      </div>
      <div class="list-cont">
        <h3></h3>
        <div class="list-main">
          <city-picker :level="1" @choice-caller="choiceCallers"></city-picker>
        </div>
        <p><strong>:level</strong></p>
        <p><strong>@choice-caller</strong></p>
      </div>
      <div class="list-cont">
        <h3></h3>
        <div class="list-main">
          <city-picker :default-city="defaultCity" :level="2"></city-picker>
        </div>
        <p><strong>:default-city</strong></p>
        <p><strong>:level</strong></p>
      </div>
      <div class="list-cont">
        <h3></h3>
        <div class="list-main">
          <city-picker :default-city="defaultCity" @visible-change="visibleChange"></city-picker>
        </div>
        <p class="careful"><strong>:level</strong></p>
        <p><strong>:default-city</strong></p>
        <p><strong>@visible-change</strong>/</p>
      </div>
      <div class="list-cont">
        <h3></h3>
        <div class="list-main">
          <city-picker :default-city="defaultCity" :short-name="true" :disabled="true"></city-picker>
        </div>
        <p><strong>:default-city</strong></p>
        <p><strong>:short-name</strong>/</p>
        <p><strong>:disabled</strong></p>
      </div>
      <div class="table">
        <h3>Citypicker Attributes</h3>
        <table>
          <tr>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
          <tr v-for="(item, index) in tableData.length" :key="index">
            <td>{{tableData[index].parameter}}</td>
            <td>{{tableData[index].explain}}</td>
            <td>{{tableData[index].type}}</td>
            <td>{{tableData[index].defaults}}</td>
          </tr>
        </table>
      </div>
      <div class="table">
        <h3>Citypicker Events</h3>
        <table>
          <tr>
            <th></th>
            <th></th>
            <th></th>
          </tr>
          <tr v-for="(item, index) in eventData.length" :key="index">
            <td>{{eventData[index].parameter}}</td>
            <td>{{eventData[index].explain}}</td>
            <td>{{eventData[index].opt}}</td>
          </tr>
        </table>
      </div>
    </div>
  </div>
</template>

<script>
import cityPicker from './components/cityPicker'

export default {
  name: 'App',
  data () {
    return {
      defaultCity: ', , ',
      storage: true,
      tableData: [
        {
          parameter: 'city-data',
          explain: '',
          type: 'Array',
          defaults: '-'
        }, {
          parameter: 'default-city',
          explain: '',
          type: 'String',
          defaults: '-'
        }, {
          parameter: 'selectpattern',
          explain: '',
          type: 'Array',
          defaults: "[{field: 'userProvinceId', placeholder: ''}, {field: 'userCityId', placeholder: ''}, {field: 'userDistrictId', placeholder: ''}]"
        }, {
          parameter: 'short-name',
          explain: '/',
          type: 'Boolean',
          defaults: 'false'
        }, {
          parameter: 'storage',
          explain: 'Name/Id',
          type: 'Boolean',
          defaults: 'true'
        }, {
          parameter: 'level',
          explain: '',
          type: 'Number',
          defaults: '3'
        }, {
          parameter: 'disabled',
          explain: '',
          type: 'Boolean',
          defaults: 'false'
        }
      ],
      eventData: [
        {
          parameter: 'choice-caller',
          explain: '',
          opt: 'values[name, id]'
        }, {
          parameter: 'visible-change',
          explain: '/',
          opt: 'visible false true'
        }
      ]
    }
  },
  components: {
    cityPicker
  },
  methods: {
    visibleChange (visible) {
      console.log(visible ? '' : '')
    },
    choiceCallers (values) {
      console.log(''+ values)
    }
  }
}
</script>

<style lang="scss">
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 3%;
}
.logo {
  width: 50px;
}
.center {
  display: inline-block;
  max-width: 1150px;
}
.list-cont {
  border: 1px solid #ebeef5;
  border-radius: 4px;
  box-shadow: 0 2px 12px 0 rgba(0,0,0,.1);
  width: 500px;
  display: inline-block;
  vertical-align: top;
  margin: 3%;
  .list-main {
    border-top: 1px solid #ebeef5;
    border-bottom: 1px solid #ebeef5;
    padding: 20px 0;
  }
  p {
    font-size: 14px;
    color: #5e6d82;
    text-align: left;
    padding: 0 3%;
    strong {
      color: #5e6d82;
      background-color: #e6effb;
      margin: 0 4px;
      display: inline-block;
      padding: 1px 5px;
      font-size: 12px;
      border-radius: 3px;
      height: 18px;
      line-height: 18px;
    }
    &.careful {
      color: #ac3797;
      strong {
        background-color: #f9dff8;
        color: #ac3797;
      }
    }
  }
}
.table {
  margin-bottom: 3%;
  h3 {
    text-align: left;
  }
  table {
    width: 100%;
  }
  th,
  td {
    border-bottom: 1px solid #ebeef5;
    padding: 15px;
    text-align: left;
    max-width: 250px;
  }
  th {
    color: #666;
  }
  td {
    color: #333;
  }
}
</style>



